/* ==================== Media queries ======================= */

/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1920px) {
	.about-section .wrapper-in {
		position: static;
		transform: none;
	}
	.about-text-cont {
		padding-left: 60px;
		width: 50%;
	}
	.central-img {
		//z-index: 0;
	}
}

@media only screen and (max-width : 1860px) {
	.slider-call {
		max-width: 30%;
	}
}

@media only screen and (max-width : 1639px) {
	.name {
		font-size: 100px;
	}
	.logo-cont img {
		max-height: 260px;
	}
	.central-img {
		width: 400px;
		top: auto;
		left: auto;

		bottom: -15px;
		right: 5%;
	}
	.about-text {
		top: 30%;
		position: static;
		transform: none;
	}
}

@media only screen and (max-width : 1439px) {
	.logo-cont {
		//top: 38%;
	}

	.slider-call span {
		height: 40px;
		line-height: 32px;
		margin-bottom: 0;
		font-size: 16px;
		background-size: 96% 80%;
	}
	.info-section .wrapper-in > img {
		max-width: 450px;
		left: auto;
		right: 6px;
		top: 30%;
	}
	.links-cont a > p {
		font-size: 25px;
	}
}

@media only screen and (max-width : 1300px) {
	.name {
		font-size: 70px;
	}
	.logo-cont p {
		font-size: 26px;
	}
	.slider-call span {
		min-width: 170px;
		width: 170px;
		padding-left: 10px;
	}
	.slider-call span:after {
		background-image: none;
		content: ">>";
		margin-left: 2px;
	}
	.slider-call:hover span {
    	background-image: none;
	}
	.links-cont a {
		max-width: 33.3%;
	}
	.footer-r ul li a:hover {
		background-image: none;
	}
	.modal-slider {
		.modal-content {
			flex-wrap: wrap;
			justify-content: center;
			overflow-y: auto;
			.modal-l {
				flex-basis: 100%;
				min-width: 100%;
				

			}
			.modal-text {
				padding: 20px 10px;
			}
			.modal-r {
				margin-bottom: 30px;
			}

		}
	}


}

@media only screen and (max-width : 1199px) {
	.header-link-cont {
		top: 10px;
	}
	.header-link-cont a:hover {
		background-image: none;
	}

	.nav-links {
		width: auto;
		li {
			//display: inline-block;
			padding: 5px !important;
			text-align: left !important;
			font-size: 16px;
			margin-bottom: 16px;
		}
	}
	.phone {
		right: 0;
		font-size: 18px;
	}
	.contact-links li:before {
		display: none;
	}
	.name {
		font-size: 40px;
	}
	.logo-cont img {
		max-height: 220px;
	}
	.logo-cont p {
		font-size: 20px;
	}
	.central-img {
		display: none;
	}
	.content {
		padding: 40px 10px;
	}
	.content:before {
		display: none;
	}
	.testimonials-section:after {
		display: none;
	}
	.download-links li a:before,
	.download-links li a:after {
		background-image: none;
	}
	.modal-l {
		//padding: 20px 10px;
	}
	.modal-text {
		padding: 0;
		font-size: 16px;
		h2 {
			font-size: 36px;
		}
		p {
			margin-bottom: 10px;
		}
	}
	.modal-service .modal-l:after {
		width: 100%;
	}


	
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
	h2 {
		font-size: 36px !important;
	}
	.logo-cont {
		top: 70px;
		transform: translateX(-50%);
		img {
			margin-bottom: 4px;
		}
	}

	.name {
		font-size: 32px;
	}
	.slider-call {
		max-width: 300px;
	}
	.nav-links li {
		margin-bottom: 6px;
	}
	.about-section {
		padding-top: 30px;
		.wrapper {
			>img {
				max-height: 400px;
				display: block;
				margin: 0 auto;

			}
			.central-img {
				display: none;
			}
		}
	}
	.about-text-cont {
		position: static;
		transform: none;
		padding: 20px 0;
		font-size: 16px;
		width: 100%;
	}
	.about-text {
		position: static;
		transform: none;
	}
	.links-cont a > p {
		font-size: 22px;
	}
	.info-section .wrapper-in > img {
		max-width: 350px;
	}
	.footer {
		padding: 20px 0;
	}
	.footer .wrapper-in {
		flex-wrap: wrap;
	}
	.footer-r {
		flex-basis: 100%;
		min-width: 100%;
		padding-top: 20px;
		
		

	}
	.footer-r ul {
		flex-wrap: wrap;
		margin-bottom: 20px;
		li {
			flex-basis: 100%;
			min-width: 100%;
			//white-space: nowrap;
			margin-bottom: 20px;
			>p,
			>a,
			>span {
				display: inline;
			}
		}
	}
	.footer-r > p {
		text-align: left;
	}
	.modal-service {
		//height: 390px;
		overflow: auto;
	}
	.modal-l {
		//overflow: auto;
		height: auto;
	}
	.modal-service .modal-l:after {
		//display: none;
	}
	.modal-service .modal-r {
		text-align: right;
	}
	.modal-service .modal-r img {
		display: inline;
		width: auto;
	}
	.sliders {
		flex-wrap: wrap;
		justify-content: center;

	}
	.slider-lg-cont {
		width: 100%;
	}
	.slider-lg-cont img {
		min-width: 0 !important;
	}
	.modal-slider-controls {
		position: static;

	}
	.modal-next {
		transform: rotate(-180deg);
		border-color: #888;
	}
	.modal-prev {
		transform: rotate(0);
		margin-right: 45px;
		border-color: #888;
	}
	.slider-sm-cont {
	display: none;
	}
	


}

@media only screen and (max-width : 800px) {
	.logo-cont img {
		max-height: 160px;
	}
}

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
	h2 {
		font-size: 30px !important;
		margin-bottom: 15px !important;
	}
	.download-links li {
		margin-bottom: 10px;
	}
	.about-text-cont h2 + p {
		margin-bottom: 10px !important;
	}
	.about-text > div {
		padding: 20px 0;
		font-size: 16px;
	}
	.about-text p {
		font-size: 16px;
	}
	.nav-links {
		display: none;
	}
	.btn-menu {
		display: block;
	}
	.header-link-cont {
		top: 0;
	}
	.contact-links {
		width: 100%;

		text-align: left;
		li {
			display: inline-block;

		}
		.phone,
		.address {
			font-family: $ur;
			font-size: 16px;
		}
		.phone {
			margin-right: 15px;
		}
	}
	.slider-call {
		display: inline-block;
		position: relative;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		max-width: 100%;
		margin: 0 auto;
		img {
			max-height: 200px;
			width: auto;
		}
		//width: 100%;
	}
	.links-cont a {
		max-width: 50%;
	}
	.info-section .wrapper-in > img {
		display: block;
		position: static;
		max-width: 94%;
		margin: 0 auto;
	}
	.download-links li a  {
		font-size: 16px;
	}
	.slide-prev,
	.slide-next {
		width: 40px;
		height: 40px;
	}
	.modal-content {
		flex-wrap: wrap;
		justify-content: center;
		//overflow: hidden;
	}
	.modal-service .modal-l,
	.modal-service .modal-r {
		width: 100%;
	}
	.modal-service .modal-r {
		order: -1;
		padding: 10px;
		img {
			max-height: 130px;
			width: auto;
		}
	}
	.modal-service .modal-l {
		padding: 20px 10px;
	}
	.modal-service .modal-l:after {
		display: none;
	}
	.modal-service .modal-r {
		text-align: left;
	} 

}

@media only screen and (max-width : 650px) {
	.slider-call img {
		max-height: 140px;
	}
	.logo-cont {
		top: 40px;
	}
	.name {
		font-size: 28px;
		margin-bottom: 0;
		.logo-cont p {
			font-size: 18px;
		}
	}
	.contact-links {
		white-space: nowrap;
	}
	.contact-links .phone,
	.contact-links .address {
		font-size: 13px;
	}
} 

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
	.header .wrapper:after {
		background-color: rgba(0,0,0,.5);
	}
	.logo-cont {
		top: 30px;
	}
	.logo-cont img {
		max-height: 100px;
	}
	.name {
		font-size: 20px;
	}
	.logo-cont p {
		font-size: 13px;
	}
	.links-cont a {
		max-width: 100%;
	}
}

/* Large Devices, Wide Screens */
@media only screen and (min-width : 1300px) {
	
	
	.logo-cont img {
		transform: translateY(-200vh);
		opacity: 0;
		animation: fromTop 1.2s;
		animation-fill-mode: forwards;
	}
	.logo-cont h1 {
		transform: translateY(200vh);
		opacity: 0;
		animation: fromBottom 1.2s;
		animation-fill-mode: forwards;
	}
	.logo-cont p {
		opacity: 0;
		animation: opacity 1.2s 1.3s;
		animation-fill-mode: forwards;
	}
	.slider-call {
		opacity: 0;
		transform: translateX(200vw);
		animation: fromRight 1.2s .5s;
		animation-fill-mode: forwards;
	}
	.links-cont a {
	  &:hover {
	    box-shadow: 10px 5px 15px rgba(0,0,0, .4), -3px 15px 15px rgba(0,0,0, .4);
	    z-index: 2;
	    &:before {
	      top: -100%;
	    }
	    &:after {
	      top: 100%;
	    } 
	    .img-cont {
	      &:after {
	        opacity: 0;
	      }
	      img {
	        transform: scale(1.08);
	      }
	    }
	    >p {
	      background-color: rgba(0,0,0,.6);
	      &:before,
	      &:after {
	        opacity: 1;
	      }
	    }
	  }
	}
	.slick-center img {
	  transform: scale(1.5);
	  box-shadow: 0 0 30px rgba(0,0,0, .3);
	}
	.testimonials-slider {
		a {
			padding: 30px 60px;
			img {
				box-shadow: 0 0 20px rgba(0,0,0, .3);
     			transition: all .8s;
			}
		}
	}
	@keyframes fromTop {
		0% {
			opacity: 0;
			transform: translateY(-200vh);
		}
		100% {
			opacity: 1;
			transform: none;
		}
	}
	@keyframes fromBottom {
		0% {
			opacity: 0;
			transform: translateY(200vh);
		}
		100% {
			opacity: 1;
			transform: none;
		}
	}
	@keyframes fromRight {
		0% {
			opacity: 0;
			transform: translateX(200vw);
		}
		100% {
			opacity: 1;
			transform: none;
		}
	}
	@keyframes opacity {
		0% {
			opacity: 0;
		}
		100% {
			opacity: 1;
		}
	}
	
}
