
/* ==================== fonts ======================= */

@font-face {
    font-family: "Airborne Pilot";
    src: url("../fonts/airbornepilot/airbornepilot.eot");
    src: url("../fonts/airbornepilot/airbornepilot.eot?#iefix")format("embedded-opentype"),
    url("../fonts/airbornepilot/airbornepilot.woff") format("woff"),
    url("../fonts/airbornepilot/airbornepilot.ttf") format("truetype");
    font-style: normal;
    font-weight: 400;
}

