
/* ==================== common styles ======================= */

html {
  overflow-x: hidden;
}

body {
  min-width: 320px;
  color: #0a0a0a;
  font-family: $ur;
  overflow: hidden;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #1e1e1e;
  opacity: .87;
  z-index: 7;
}


a {
  text-decoration: none;
  color: inherit;
  
}
a,
img {
  outline: none;
  border: none;
  max-width: 100%;
}

input,
input:focus {
  outline: none;
}
h2 {
  font-family: $abp;
  font-size: 48px;
  text-transform: uppercase;
  margin-bottom: 12px;
}

section {
  position: relative;
  //padding: 20px 0;
}

.wrapper {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
}

.wrapper-in {
  position: relative;
  width: 96%;
  max-width: 1110px;
  //max-width: 96px;
  margin: 0 auto;
}
strong {
  font-weight: 700;
}
/*--------------------------  --------------------------*/

.header {
  position: relative;
  color: #fff;
  background-color: #f6f6f6;
  .wrapper {
    position: relative;
    text-align: center;
    overflow: hidden;
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(0,0,0,.3);
      background-image: url(../img/common/pattern.png);
      background-repeat: repeat;
    }
    
    
  }


  .wrapper-in {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 20px;
  }
  img {
    display: block;
  }

}
.slider-call {
  display: block;
  position: absolute;
  bottom: 0;

  right: 0;
  overflow: hidden;
  z-index: 1;
  img {
    display: block;
    width: 100%;
    transition: all .8s;
  }
  span {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 5px;
    font-family: $abp;
    font-size: 18px;
    text-transform: uppercase;
    height: 80px;
    line-height: 80px;
    min-width: 255px;
    text-align: center;
    background-color: #0a0a0a;
    white-space: nowrap;
    &:after {
      content: "";
      display: inline-block;
      width: 40px;
      height: 11px;
      background-image: url(../img/common/arr.png);
      background-repeat: no-repeat;
      margin: 0 0 3px 20px;
    }
  }
  &:hover {
    img {
      transform: scale(1.04);

    }
    span {
      background-image: url(../img/common/frame.png);
      background-size: 90% 80%;
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
  }
}
.header-link-cont {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 44px;
  left: 0;
  right: 0;
  width: 100%;

  a {
    display: inline-block;
    padding: 7px 6px 6px;
    position: relative;


  &:hover {
    background-image: url(../img/common/frame.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;

    }
  }
}

.nav-links {
  width: 350px;
  position: relative;
  left: -3px;
  padding-top: 9px;
  &:after {
    content: "";
    display: block;
    height: 72%;
    width: 1px;
    background-color: #949493;
    position: absolute;
    top: 11%;
    left: 51%;
  }
  li {
    font-family: $abp;
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 22px;
    white-space: nowrap;
    &:nth-child(odd) {
      text-align: right;
      padding-right: 52%;
    }
    &:nth-child(even) {
      text-align: left;
      padding-left: 53%;
    }
  }
}

.contact-links {
  text-align: right;
  li {
    &:before {
      content: "";
      display: inline-block;
      width: 20px;
      height: 20px;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      margin-right: 30px;
    }
  }
}

.phone {
  font-family: $ub;
  font-size: 24px;
  position: relative;
  right: -4px;
  //margin-bottom: 3px;
  &:before {
    background-image: url(../img/common/phone.png);
  }
}

.address {
  font-family: $ul;
  font-size: 18px;
  padding-right: 6px;
  &:before {
    background-image: url(../img/common/pin.png);
  }
}

.logo-cont {
  position: absolute;
  top: 42.7%;

  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  img {
    display: block;
    margin: 0 auto 22px;
  }
  p {
    font-family: $ul;
    font-size: 30.6px;
    text-transform: uppercase;
  }
}

.name {
  font-family: $abp;
  font-size: 128px;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 13px;
}


.about-section {
  min-height: 500px;
  .wrapper {
    >img {
      display: block;
    }
  }
  .wrapper-in {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.about-text-cont {
    padding: 30px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 855px;
    overflow: hidden;
    background-color: #fff;
    //width: 65.6%;
    padding-left: 28%;
    z-index: 1;
    h2 {
      //color: #0a0a0a;
      margin-bottom: 9px;
    }
    h2+p {
      margin-bottom: 21px;
    }

  }
.about-text {
  position: absolute;
  top: 52%;
  transform: translateY(-50%);
  
  >div {
    position: relative;
    padding: 36px 98px 44px 0;
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 100vw;
      background-color: #f6f6f6;
      z-index: -1;
    }
  }
  p {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 27px;
    font-family: $um;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.central-img {
  position: absolute;
  top: 16.5%;
  left: 13.5%;
  z-index: 3;
}

.links-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  a {
    display: block;
    position: relative;
    // top: 0;
    // left: 0;
    flex: 1 1 100%;
    max-width: 25%;
    overflow: hidden;
    transition: all .8s;
    background-color: #000;
    //box-shadow: 0 0 0 transparent, 0 0 0 transparent;

    &:before,
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      //bottom: 0;
      left: 0;
      height: 100%;
      width: 100%;
      //background-color: rgba(0,0,0,.4);
      transition: all 1s;

    }
    &:before {
      background: repeating-linear-gradient(90deg, transparent, transparent 10%, rgba(0,0,0, .4) 10%, rgba(0,0,0, .4) 20%);
      z-index: 1;

    }
    &:after {
      background: repeating-linear-gradient(90deg, rgba(0,0,0, .4), rgba(0,0,0, .4) 10%, transparent 10%, transparent 20%);

    }
    
    
    .img-cont {
      display: block;
      position: relative;
      
      &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: url(../img/common/pattern.png);
        background-repeat: repeat;
        //transition: all .8s;

      }
    }
    img {
      display: block;
      width: 100%;
      transition: all 1.2s;
    }
    >p {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-family: $abp;
      font-size: 30px;
      line-height: 1.2;
      text-transform: uppercase;
      color: #fff;
      text-align: center;

      max-width: 370px;
      min-width: 60%;
      padding: 30px 30px;
      transition: all .2s .3s;
      z-index: 1;
      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 90%;
        height: 11px;
        background-size: 100% 100%;
        opacity: 0;
        transition: all .4s .3s;
      }
      &:before {
        top: 12px;
        background-image: url(../img/common/frame-top.png);
      }
      &:after {
        top: auto;
        bottom: 12px;
        background-image: url(../img/common/frame-bottom.png);
      }
    }
  }

}

.btn-menu {
  display: none;
  position: fixed;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  background-color: rgba(0,0,0,.4);
  border-radius: 4px;
  cursor: pointer;
  z-index: 2;
}

.btn-menu span {
  display: inline-block;
  background: #fff;
  position: absolute;
  top: 18px;
  left: 5px;
  right: 5px;
  width: 30px;
  height: 4px;
}

.btn-menu span:before, .btn-menu span:after {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0px;
  right: 0px;
  width: 30px;
  height: 4px;
  background: #fff;
}

.btn-menu span:before {
  top: -10px;
}

.btn-menu span:after {
  top: 10px;
}

.mob-cont {
  position: fixed;
  width: 320px;
  background-color: #fff;
  top: 0;
  bottom: 0;
  right: -350px;
  overflow-y: auto;
  padding: 20px 15px 20px 30px;
  -webkit-box-shadow: -2px 5px 14px 3px #333;
          box-shadow: -2px 5px 14px 3px #333;
  z-index: 9;
  color: #0a0a0a;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}

.mob-cont.active {
  right: 0;
}

.mob-cont li {
    display: block;
    font-family: "Airborne Pilot", sans-serif;
    font-size: 20px;
    text-transform: uppercase;
    margin-bottom: 60px;
    white-space: nowrap;
}


.mob-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 40px;
}


.close {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  background-color: rgba(0,0,0,.3);
  color: #fff;
  border-radius: 4px;
  text-align: center;
  font-size: 36px;
  cursor: pointer;
}

