.info-section {

  .wrapper-in {
    position: relative;

    >img {
      position: absolute;
      top: 8.3%;
      left: 40%;
    }
  }
  h2 {
    margin-bottom: 86px;
  }
}
.content {
  width: 100%;
  max-width: 855px;
  padding: 96px 0 107px;
  position: relative;
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    background-color: #f6f6f6;
    z-index: -1;

  }
}

.download-links {
  
  
  li {
    margin-bottom: 25px;
    
    a {
      display: inline-block;
      left: -5px;
      max-width: 330px;
      min-height: 95px;
      font-family: $abp;
      font-size: 18px;
      text-transform: uppercase;
      line-height: 1.5;
      padding: 18px 5px 10px 100px;
      position: relative;
      &:before,
      &:after {
        content: "";
        display: none;
        position: absolute;
        left: 0;
        right: 0;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        height: 14px;
        width: 100%;

        //opacity: 0;
        //transform: scale(1.1);
        //transition: all .4s;
      }
      &:before {
        top: 0;
        background-image: url(../img/common/frame-dark-top.png);
      }
      &:after {
        bottom: 0;
        background-image: url(../img/common/frame-dark-bottom.png);
      }
      &:hover {
        &:before,
        &:after {
          display: block;
          //opacity: 1;
          //transform: scale(1);
        }
      }
      img {
        display: block;
        width: 66px;
        max-height: 75px;
        position: absolute;
        top: 10px;
        left: 5px;
      }
    }
  }
}

