.testimonials-section {
  padding: 90px 0 55px;
  overflow: hidden;
  position: relative;
  h2 {
    text-align: center;
    margin-bottom: 35px;
  }
  &:after {
    content: "";
    display: block;

    width: 1110px;
    height: 1110px;
    border-radius: 50%;
    background-color: #f3f3f3;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;


  }
}

.slider-cont {
  margin: 0 auto;
  width: 96%;
  max-width: 1420px;
}

.testimonials-slider {
  a {
    display: block;
    padding: 10px;
    img {
      display: block;
      width: 100%;
      
    }
  }
}



.slider-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 96%;
  max-width: 920px;
  padding-top: 30px;
  margin: 0 auto;
}

.dots-cont {
  padding: 0 15px;
}

.slide-prev,
.slide-next {
  width: 56px;
  height: 56px;

  border-radius: 50%;
  border: 1px solid #bfbfbf;
  background-image: url(../img/common/slider-arr.png);
  background-repeat: no-repeat;
  background-position: 42% 50%;
  transition: all .4s;
  cursor: pointer;
  &:hover {
    border-color: #202020;
  }

}

.slide-next {
  transform: rotate(180deg);
}

.footer {
  padding: 50px 0 46px;
  color: #fff;
  background-color: #191919;
  .wrapper-in {
    display: flex;
    justify-content: space-between;
    
  }
}

.footer-l {
  // display: flex;
  // align-items: center;
  margin-top: 3px;
  max-width: 83px;
  margin-right: 20px;

}

.footer-r {
  flex-grow: 1;
  max-width: 825px;
  ul {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 74px;
    
    li {
      max-width: 190px;
      p {
        font-family: $abp;
        font-size: 18px;
        text-transform: uppercase;
      }
      a,
      span {
        display: block;
        position: relative;
        left: -5px;
        padding: 5px;
        //font-family: $um;
        font-size: 18px;
        line-height: 1.2;
      }
      a:hover {
        background-image: url(../img/common/frame.png);

        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: 100% 100%;

      }
    }
  }
  >p {
    font-family: $ul;
    font-size: 16px;
    letter-spacing: 1px;
    text-align: right;
  }
}

.modal-wrapper {
  width: 1110px;
  max-width: 90%;
  background-color: #fff;
  
  max-height: 84vh;
  padding: 0 !important;
  z-index: 6;
}

.modal-content {
  //position: fixed;
  //top: 50%;
  //left: 50%;
  //transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
  
  
  font-family: $ur;
  font-size: 18px;
  line-height: 1.5;
  height: 100%;
  h2 {
    line-height: 1.2;
    margin-bottom: 20px;
  }
  p,
  ul {
    margin-bottom: 27px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  li {
    &:before {
      content: "-";
      margin-right: 5px;
    }
  }
}

.modal-l {
  position: relative;
  //width: 50%;
  height: 100%;
}



.modal-text {
  padding: 70px 36px;
  height: 100%;
  overflow-y: auto;

}

.modals-container {
  opacity: 0;
  position: absolute;
  left: -1000vw;
}

.modal-service {
  height: 627px;
  .modal-l,
  .modal-r {
    width: 50%;
  }
  .modal-l {
      &:after {
        content: "";
        display: block;
        width: calc(100% - 17px);
        
        height: 30%;
        position: absolute;
        bottom: 0;
        left: 0;
        //right: 0;
        background: linear-gradient(180deg, transparent, #fff 80%, #fff);
    }
  }
  .modal-r {
    img {
      display: block;
      width: 100%;
      max-height: 100%;
    }
  }
}

.modal-slider {
  .modal-l {
    width: 380px;
    h2 {
      max-width: 190px;
    }
  }
  .modal-r {
    width: 730px;
    overflow: hidden;
  }
  .modal-text {
    padding-left: 60px;
  }
}
.sliders {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 100%;
}

.slider-lg-cont {
  width: 570px;
  img {
    min-width: 570px !important;
    position: relative;
    right: -1px;
  }
}

.slider-sm-cont {
  width: 160px;
  img {
    min-width: 160px !important;
    cursor: pointer;
  }
}

.slick-list  {
  //overflow: visible !important;
}

.modal-slider-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 160px;
  height: 72px;
  background-color: #fff;
  
}

.modal-prev,
.modal-next {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid transparent;
  background-image: url(../img/common/slider-arr.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  cursor: pointer;
  transition: all .4s;
  &:hover {
    border-color: #888;
  }
}

.modal-prev {
  transform: rotate(90deg);
  margin-right: 8px;
}

.modal-next {
  transform: rotate(-90deg);
}